							<li class="nav-item">
								<a href="#">
									<!-- <a id="gwc" class="nav-link" href="#">购物车({{shopCarCommoditys.length}})</a> -->
									<TopItem>
										<span slot="title">购物车({{shopCarCommoditys.length}})</span>
										<div slot="main">
											<div class="item" v-for="(shopCarCommodity,index) in shopCarCommoditys" :key="shopCarCommodity.id">
												<span :class="offClass" ref="myinput" @click="changeState($event,index,shopCarCommodity)"></span>
												<img :src="shopCarCommodity.scCommodityImg"/>
												<div class="last">
													<p>{{shopCarCommodity.commodity.commodityName}}</p>
													<p>{{shopCarCommodity.commodity.describe}}</p>
													<p style="text-align: right;">
														<!-- 商品nowprice为0或者null则表示没有优惠 -->
														<span v-if="shopCarCommodity.commodity.nowPrice == null || shopCarCommodity.commodity.nowPrice == 0">
															<span class="nowprice">￥{{shopCarCommodity.commodity.price}}</span>&nbsp;
														</span>
														<span v-else>
															<span class="nowprice">
																￥{{shopCarCommodity.commodity.nowPrice}}
															</span>
															&nbsp;
															<span class="oldprice">
																<del>￥{{shopCarCommodity.commodity.price}}</del>
															</span>
														</span>
														&nbsp;&nbsp;
														<span>
															x {{shopCarCommodity.scCommodityNumber}}
														</span>
													</p>
												</div>
