		<!-- <router-view></router-view> -->
		<Search v-show="showSearch"></Search>
		<TopBar  v-show="!showSearch"/>
	</div>

</template>

<script>
import TopItem from './TopItem.vue';
import qs from 'qs'
import axios from 'axios';
import Search from '../../views/other/Search.vue';
import TopBar from '../../views/other/TopBar.vue';
	export default {
		//给组件起一个名字,开发者工具中显示这个组件的时候，显示的就是这个名字
		name: "Header",
		data() {
			return {
				shopCarCommoditys:[],
				offClass:"myinput iconfont icon-choose1",
				onClass:"myinput iconfont icon-choose",
				selectedList:[],
				sum:0,
				Logined:false,
				showSearch:false,
				myClass:'default'
			}
		},
		components:{
			TopItem,
			Search,
			TopBar
		},
		computed:{
			
		},
		mounted(){
			//全局事件绑定添加购物车成功之后的购物车所有商品
			this.$bus.$on('sentShopCarCommoditys',(shopCarCommoditys)=>{
				this.shopCarCommoditys = shopCarCommoditys
			})
			const user = JSON.parse(localStorage.getItem("huawei_login"))
			if(user != null){
				axios.get(`/getAllShopCarCommodity/${user.id}`).then((response)=>{
				console.log(response.data.result);
				this.shopCarCommoditys = response.data.result
				})	
			}
			this.$bus.$on("changeShow",()=>{
				this.showSearch = !this.showSearch
			})
			console.log(this.$route.path);
			if(this.$route.path == "/search"){
				this.showSearch = true
			}
		},
		methods:{
			toLogin(){
				if(this.Logined){
					return
				}else{
					this.$router.push({
						path:'/login'
					})
				}
			},
			changeState(event,index,shopCarCommodity){
				let checks = this.$refs.myinput
				if(event.target.className==this.onClass){
					checks[index].className = this.offClass
					let i = this.selectedList.indexOf(shopCarCommodity.shopCarId)
					this.selectedList.splice(i,1)
					this.sum -= shopCarCommodity.scCommodityPrice
				}else{
					checks[index].className = this.onClass
					this.selectedList.push(shopCarCommodity.shopCarId)
					this.sum += shopCarCommodity.scCommodityPrice
				}
				console.log("this.selectedList",this.selectedList);
			},
			sendCommoditys(){
				const user = JSON.parse(localStorage.getItem("huawei_login"))
				let SCCommodityListStr = JSON.stringify(this.selectedList)
				let dfk = false
				if(this.selectedList.length == 0){
					alert("请至少选择一项")
					return
				}
				if(confirm('是否付款成功？')){
					dfk = false				
				}else{
					dfk = true
				}
				if(user != null){
					axios.post(`/addOrderOfShopCar?orderPrice=${this.sum}&dfk=${dfk}&userId=${user.id}`,
					//通过qs这个工具类将数组类型的数据传递给后端 
					qs.stringify({ SCCommodityList: SCCommodityListStr }, { indices: false })
					).then((response)=>{
					console.log(response.data.result);
					this.shopCarCommoditys = response.data.result
				})
				}
			},
			mouseover(){
				if(this.Logined){
					this.myClass = "show"
				}
				
			},
			mouseleave(){
				this.myClass = "default"
			},
			loginout(){
				//退出登录需要干嘛?
				axios({
					method:'get',
					url:'/loginOut'
				})

				localStorage.clear()
				
				if(this.$route.path != '/home'){
					this.$router.push({
						path:'/home'
					})
				}else{
					console.log("退出登录");
					this.Logined = false
					this.mouseleave()
					this.shopCarCommoditys = []
				}
			},
			isLogin(){
				let user = JSON.parse(localStorage.getItem("huawei_login"))
				console.log(user);
				if(user==null){
					return "请登录..."
				}else{
					this.Logined = true
					return user.userName + "已登录"
				}
			}
		}
	};
</script>
