<template>
    <div>
        <div class="topover">
            <div class="top">
                <div class="container">
                    <div class=" text-center mt-5 pt-5 h2">
                        华为账号注册
                    </div>

                    <div class=" text-center d-flex justify-content-center mt-5 pt-5">
                        <div class="row">
                            <div class="a8 col">
                                <img class="shadow rounded" src="/images/login/erweima.png" />
                                <p class="a1 mt-5 pt-2">若您使用华为手机，请进入“设置”>“华为帐号”扫码登录。</p>
                            </div>
                            <div class="a3 col ">
                                <!-- <form action="" class=""> -->
                                <input type="text" @focusout="checkUsername" class="a2 mb-4 ml-5 form-control" v-model="username"
                                    placeholder="手机号/邮件地址/账号名">
                                <input @focusout="checkPassword" type="password" class="a2 ml-5 mb-3 form-control" v-model="firstPassword"
                                    placeholder="密码">
                                <input @focusout="checkPassword" type="password" class="a2 ml-5 mb-3 form-control" v-model="secondPassword"
                                placeholder="确认密码">
                                <span v-show="show" href="#" :class="dangerClass">{{message}}</span><br>
                                <div class="a5 ml-5 mt-4  form-control" @click="register" value="注册">注册</div>
                                <!-- </form> -->
                                <div class="a6 mt-4 ">
                                    <span class=" pr-3">
                                        <router-link to="/login" replace>
                                            已有账号立即登录
                                        </router-link>
                                    </span>

  
                                </div>
                                <div class="a6 mt-4 ">
                                    <a href="#"> <img class="rounded-circle" src="/images/login/ali.png" /></a>
                                    <a href="#"> <img class="rounded-circle" src="/images/login/qq.png" /></a>
                                    <a href="#"> <img class="rounded-circle" src="/images/login/weixin.png" /></a>
                                </div>
                            </div>
                        </div>

                    </div>


                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import axios from 'axios';
    export default {
        //给组件起一个名字,开发者工具中显示这个组件的时候，显示的就是这个名字
        name: "login",
        data() {
            return {
                username: '',
                goodUsername:false,
                firstPassword: '',
                secondPassword:'',
                show:true,
                dangerClass:'a4 text-primary',
                message:'',
            };
        },
        methods: {
            register() {
                this.checkUsername()
                console.log(this.firstPassword);
                console.log(this.secondPassword);
                console.log("zxczxczxzcxc",this.firstPassword != this.secondPassword);
                if(this.firstPassword != this.secondPassword){
                    this.message = '两次输入不一致!'
                    this.show = true
                    this.dangerClass = 'a4 text-primary danger'
                    return
                }
                if(this.firstPassword.trim() == this.secondPassword.trim() && this.goodUsername){
                    axios({
                        method:'post',
                        url:`/register/${this.username}/${this.firstPassword}`,
                    }).then((response)=>{
                        console.log(response.data.result);
                        if(response.data.responseCode==200){
                            alert("注册成功!即将为为你自动跳转登录页")
                            setTimeout(()=>{
                                this.$router.push({
                                    path:'/login'
                                })
                            },2000)
                        }
                    })
                }else{
                    this.dangerClass = 'a4 text-primary danger'
                }
            },
            checkPassword(){
                if(this.firstPassword.trim().length < 6  || this.secondPassword.trim().length < 6){
                    this.message = '密码至少6位'
                }else{
                    if(this.firstPassword.trim() != this.secondPassword.trim()){
                        this.message = '两次输入不一致!'
                        this.show = true
                    }else{
                        this.message = '密码合法'
                        this.show = true
                        this.dangerClass = 'a4 text-primary good'
                    }
                }
                    
               
            },
            checkUsername(){
                console.log("检查用户名被处罚");
                if(this.username.length < 4){
                    this.message = '用户名至少四个字符'
                    this.show = true
                    this.goodUsername = false
                }else{
                    axios({
                        method:'post',
                        url:`/checkUsername/${this.username}`
                    }).then((response)=>{
                        this.goodUsername = response.data
                        console.log(response.data);
                        if(response.data == false){
                            this.message = '用户名已存在,换一个试试吧'
                            this.show = true
                            this.goodUsername = false
                        }
                    })
                }
            }
        },
        mounted() {
        },

    };
</script>

<style scoped>
    .topover {
        width: 100%;
        background: rgb(242, 242, 242);
    }
    .good{
        color: green !important;
    }

    /* .top {
    width: 1200px;
} */
    .container {
        width: 1200px;
        height: 740px;
        margin: 0 auto;
        overflow: hidden;
    }

    /*规范页面最小宽度1200px */

    .a1 {
        font-size: 13px;
    }

    .a2 {
        height: 48px;
        width: 358px;

    }

    .a3 {
        margin-left: 60px;
        padding-left: 60px;
        border-left: solid 1px rgb(141, 137, 137);
    }

    .a3 input {
        border: 0px;
    }

    .a3 input:focus {
        box-shadow: none;
    }


    .a4 {
        font-size: 14px;
        float: left;
        text-decoration: none;
    }
    .danger{
        color: red !important;
    }




    .a5 {
        width: 360px;
        height: 48px;
        color: aliceblue;
        background-color: red;
        font-size: 18px;

    }

    .a6 a {
        text-decoration: none;
        font-size: 18px;
        padding: 0px 20px 0px 20px;
    }

    .a7 {
        border-left: 1px solid;
        border-color: rgb(36, 36, 36);
    }
</style>