<template>
  <div id="demo" class="carousel slide" data-bs-ride="carousel">
    <!-- 轮播图片 -->
    <div class="carousel-inner big">
        <img :src="currentImg"/>
    </div>
      
    <!-- 指示符 -->
    <div class="foot">
      <div class="img_out">
        <span class="iconfont icon-return icon_class" @click="preImg"></span>
        <!-- <div> -->
          <img v-for="(img,index) in imageslist" :key="index" :src="img" @mouseover="changeIndex(index)"/>
        <!-- </div> -->
        <span class="iconfont icon-enter icon_class"  @click="nextImg"></span>
      </div>
    </div>
  </div>
</template>
<script>
import axios from 'axios'
export default {
  data() {
    return {
      //接收当前商品的所有图片路径
      imageslist: [],
      currentIndex:0
    }
  },
  methods: {
    changeIndex(index){
      this.currentIndex = index
    },
    //上一张图片
    preImg(){
      //下标可以到0
      if(this.currentIndex >= 1){
        this.currentIndex--
      }else{
        //下标不可以到长度  最大为长度-1
        this.currentIndex = this.imageslist.length-1
      }
    },
    //下一张图片
    nextImg(){  
      //下标可以到0
      if(this.currentIndex >= this.imageslist.length-1){
        this.currentIndex = 0
      }else{
        //下标不可以到长度  最大为长度-1
        this.currentIndex++
      }
    }
  },
  computed:{
    //初始化的时候这样才能从数组中拿出数据
    currentImg(){
      return this.imageslist[this.currentIndex]
    }
  },
  mounted() {
      //定义全局事件总线接收当选择颜色时返回的图片地址
      this.$bus.$on("images",(images)=>{
        this.imageslist = images
      })
  },

}
</script>

<style lang="css" scoped>
.carousel-inner img {
  width: 100%;
  height: 100%;
}

.foot {
  margin-top: 22px;
  width: 461px;
  height: 81px;
  /* background-color: bisque; */
}


.foot img {
  width: 80px;
  height: 80px;
}

.big {
  width: 500px;
  height: 500px;
}

.carousel-item {
  width: 100%;
  height: 100%;
}
.img_out{
  display: flex;
  justify-content: space-between;
}

img:hover{
  /* border: 1px solid red; */
  outline: 1px solid red;
}
.icon_class{
  font-size: 30px;
  height: 100%;
  line-height: 80px;
  color: rgb(209, 209, 209);
}
</style>