import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter)

import Home from '../views/out-main/Home.vue'
import TopBar from '../views/other/TopBar.vue'
import Search from '../views/other/Search.vue'
import CommDetail from '../views/CommDetail/CommDetail.vue'
import Pay from '../views/pay/Pay.vue'
import Login from '../views/login/login.vue'
import OrderOut from '../views/order/OrderOut.vue'
import OrderLast from '../views/order/OrderLast.vue'
import OrderAgo from '../views/order/OrderAgo.vue'
import ShopCar from '../views/shopCar/ShopCar.vue'
import SecCommDetail from '../views/CommDetail/SecCommDetail.vue'
import SearchMain from '../views/search/SearchMain.vue'
import Register from '../views/register/Register.vue'

const routes = [
  {
    path: '/',
    redirect: '/home'
  },
  {
    path: '/home',
    component: Home,
  },
  {
    path:'/search',
    component:SearchMain
  },
  {
    path: '/commDetail/:commodityId',
    name:'commodityDetail',
    component: CommDetail,
  },
  {
    path: '/secCommDetail/:commodityId',
    name:'secCommDetail',
    component: SecCommDetail,
  },
  {
    path:'/pay',
    component:Pay,
    meta:{
      needLogin:true
    }
  },
  {
    path:'/login',
    component:Login,
  },
  {
    path:'/order',
    component:OrderOut,
    redirect:'/order/last',
    meta:{
      needLogin:true
    },
    children:[
      {
        path:'last',
        component:OrderLast,
        meta:{
          needLogin:true
        }
      },
      {
        path:'ago',
        component:OrderAgo,
        meta:{
          needLogin:true
        }
      }
    ]
  },
  {
    path:'/shopCar',
    component:ShopCar,
    meta:{
      needLogin:true
    }
  },
  {
    path: '/topbar',
    component: TopBar
  },
  {
    path: '/search',
    component: Search
  },
  {
    path:'/register',
    component:Register
  }

]

const router = new VueRouter({
  mode: 'hash',
  base: process.env.BASE_URL,
  routes
})

router.beforeEach((to, from, next) => {
  const userState = localStorage.getItem("huawei_login")
    if(to.meta.needLogin == true && userState == null){
      console.log("想要强制跳转");
      next("/login")
    }else{
      next()
    }
    next()
})

export default router
