<template>
    <div class="search-out">
        <div class="center-input">
            <div class="inputbox">
                <input @keyup.enter="search" v-model="keywords" type="text">
                <span @click="changeShow" class="ph"><span class="iconfont icon-delete text"></span></span>
            </div>
        </div>
    </div>
</template>
<script>
import axios from 'axios';
    export default {
        name: 'search',
        data() {
            return {
                keywords:''
            }
        },
        methods:{
            search(){
                //全局事件总线将关键词传递
                this.$store.state.keyWord = this.keywords
                axios({
                method:'get',
                    url:`/likeGet?word=${this.keywords}`
                }).then((response)=>{
                    console.log("查询到的数据",response.data.result);
                    if(this.$route.path != "/search"){
                        this.$router.push({
                            path:'/search'
                        })
                    }
                    //全局事件触发 将结果传到展示的组件中
                    this.$bus.$emit("sendSearchResult",response.data.result)
                    
                })
            },
            changeShow(){
                if(this.$route.path == "/search"){
                    this.$router.push({
                        path:'/home'
                    })
                }
                this.$bus.$emit("changeShow")
            }
        },
        mounted(){
            console.log("vuex中的关键子",this.$store.state.keyWord);
            console.log("测试的位置的 this.$route.path", this.$route.path);
            if(this.$store.state.keyWord != null && this.$route.path == "/search"){
                console.log("判断进来了");
                this.keywords = this.$store.state.keyWord
                axios({
                method:'get',
                    url:`/likeGet?word=${this.$store.state.keyWord}`
                }).then((response)=>{
                    console.log("通过vuex关键字查出来的数据",response.data.result);
                    //全局事件触发 将结果传到展示的组件中
                    this.$bus.$emit("sendSearchResult",response.data.result)
                    if(this.$route.path != "/search"){
                        this.$router.push({
                            path:'/search'
                        })
                    }
                    
                    
                })
            }
        }
    };
</script>
<style scoped>
    .search-out {
        height: 80px;
        display: flex;
        flex-direction: row;
        justify-items: center;
    }

    .center-input {
        height: 46px;
        width: 656px;
        /* background-color: red; */
        margin: auto;
        border: none;
        position: relative;
    }

    .inputbox {
        display: inline-block;
        /* float: left; */
        height: 44px;
        width: 548px;
        /* position: relative; */
    }

    .ph {
        display: inline-block;
        height: 24px;
        width: 22px;
        /* background-image: url(img/nicon.png); */
        background-position: 0px 50px;
        position: absolute;
        right: 10px;
        top: 8px;
        color: #c4c7ce;
        text-decoration: none;
    }

    input {
        height: 44px;
        width: 656px;
        border: 2px solid #c4c7ce;
        box-shadow: none;
        border-radius: 10px 10px 10px 10px;
        /* border-right: none; */
        background: #fff;
        font-size: 16px;
        padding: 12px 16px;
        float: left;
        outline: 0;
    }

    input:hover {
        border-color: #a7aab5;
    }

    input:focus {
        border-color: #a7aab5;
    }
    .text{
        font-size: 30px;
        position: relative;
        top: -8px;
        right: 8px;
    }
</style>