import Vue from 'vue'
import App from './App.vue'
import router from './router'
import 'jquery/dist/jquery.min'
import 'bootstrap/dist/css/bootstrap.css'
import './assets/fonts/iconfont.css'
import store from './store/store.js'

import ElementUI from 'element-ui';                      // 引入element-ui
import 'element-ui/lib/theme-chalk/index.css'; 

//vue中引用NProgress加载进度条
import NProgress from 'nprogress'
import 'nprogress/nprogress.css'


import axios from 'axios';
axios.defaults.withCredentials=true;
// 请求拦截器
axios.interceptors.request.use(
	config => {
    // axios({
    //   url:`/commodity/checkToken`,
    // })
		// if (localStorage.getItem("token") != null&&
    // localStorage.getItem("token") != undefined
    // // &&config.url.indexOf('user/login') == -1
		// // token如果存在并且不是登录接口，则统一在http请求的header都加上token，这样后台根据token判断你的登录情况，
		// //在这里插一嘴axios的请求设置头name后台接收时必须也设置接收这个请求头name（一一对应）,否者会报415
		// ) {
      console.log("设置了token");
      // let tokenValue = encodeURIComponent(localStorage.getItem("huawei_token"))
			config.headers["token"] = localStorage.getItem("huawei_token")
      // if
		// }
		return config;
	},
	err => Promise.reject(err)
);

// response 拦截器中,  隐藏进度条NProgress.done()
axios.interceptors.response.use(config => {
  NProgress.done()
  console.log("响应拦截器中得到config.config.url:",config.config.url.indexOf("/getAllShopCarCommodity") != -1);
  // if(config.data.responseCode == 442 && ){
  if(config.data.responseCode == 442 &&
     (config.config.url.indexOf("/checkToken") != -1 || config.config.url.indexOf("/getAllShopCarCommodity") != -1)){
    alert("登录过期")
    localStorage.clear()
    router.push("/login")
  }
  return config
})


Vue.config.productionTip = false
Vue.use(ElementUI)

new Vue({
  router,
  render: function (h) { return h(App) },
  store: store,
  beforeCreate() {
    //钩子函数中此时的this指向就是当前vm
    Vue.prototype.$bus = this
  }
}).$mount('#app')
