  <script>
import Lists from "./ShopCarItemList.vue";
  export default {
    data() {
      return {
        isChecked:false
      };
    },
    components: {
      Lists,
    },
    methods: {
      allSelect(e){
        this.$bus.$emit('allSelect',e.target.checked)
        
      },
    },
    mounted() {
      this.$bus.$on("changeAllChecked",(checked)=>{
          this.isChecked = checked
        })
    },
  };
  </script>
  
