<template>
    <div class="top-item-out">
        <router-link to="/ShopCar">
            <a class="title" @mouseover="mouseover" @mouseleave="mouseleave" @click="click">
                <slot name="title">默认标题</slot>
            </a>
        </router-link>
        
        <div :class="mainClass" ref="main"  @mouseover="mouseover" @mouseleave="mouseleave">
            <slot name="main">默认内容填充</slot>
        </div>
    </div>
</template>
<script>

export default{
    data(){
        return{
            mainClass: 'off'
        }
    },
    methods:{
        mouseover(){
            if(this.$route.path != '/ShopCar'){
                this.mainClass = "on"
            }
        },
        mouseleave(){
            this.mainClass = "off"
        },
        click(){
            this.mainClass = "off"
        }
    }
}
</script>
<style scoped>
.top-item-out{
    position: relative;
    overflow: visible;
}
.title{
    text-decoration: none !important;
    /* 去除默认的下划线 */
    font-size: 10px;
    color: rgb(153, 153, 153) !important;
    padding: 9px 8px;
    font-weight: 700 !important;
    line-height: 36px;
}
/* 通过vue改变on和off这两个准备好的类，不要删除 */
.off{
    color: black !important;
    display: none;
    position: absolute;
    background-color: white;
    top: 35px;
    right: 0px;
    z-index: 100;
    width: 460px;
    max-height: 500px;
    min-height: 500px;
    overflow: scroll;
    overflow-x:hidden;

}
/* 通过vue改变on和off这两个准备好的类，不要删除 */
.on{
    color: black !important;
    position: absolute;
    background-color: white;
    display: block;
    top: 35px;
    right: 0px;
    z-index: 100;
    width: 460px;
    max-height: 500px;
    overflow: scroll;
    overflow-x:hidden;

}
.top-item-out:hover{
    background-color: white;
}
a{
    text-decoration: none !important;
	color: aliceblue !important;
}

</style>