<template>
<div>
    <CommDetailRightTop :commodity="commodity"/>
    <CommDetailRightButton  :commodity="commodity"/>
</div>
</template>
<script>
import CommDetailRightTop from './CommDetailRightTop.vue'
import CommDetailRightButton from './CommDetailRightButton.vue'
import axios from 'axios'
export default{
    data() {
        return {
            commodity:{},
            
        }
    },
    components:{
        CommDetailRightTop,
        CommDetailRightButton
    },
    mounted(){
        axios({
            method:'get',
            url:'/commodityDetail/' + this.$route.params.commodityId
        }).then((response) => {
            // console.log(response.data.result);
            this.commodity = response.data.result
        })
    },
    computed:{
        pay_get_price(){
            return this.commodity.nowPrice == null || this.commodity.nowPrice == 0?this.commodity.price:this.commodity.nowPrice
        }
    },
}
</script>
