<template>
  <div class="out">

    <!-- 导航栏 -->
    <Top/>
    <!-- 搜索框 -->
    <!-- <TopBar/> -->
    
    <!-- 轮播 -->
    <Carousel />
    <!-- 秒杀 -->
    <ComPartition sortName="秒杀">
        <div class="seckill_commodity">
            <SeckillTime/>
            <SeckillCommodity class="" v-for="seckillCommodity in seckillCommodityList" :key="seckillCommodity.id" :commodity="seckillCommodity"></SeckillCommodity>
        </div>
    </ComPartition>
    <!-- 通过遍历将所有分类展示出来 -->
    <ComPartition v-for="sort in sortList" :key="sort.id" :sortName="sort.sort">
        <div>
          <!-- 通过遍历将一个分类下的所有商品展示出来 -->
          <Commodity v-for="commodity in sort.commodityList" :key="commodity.id" :commodity="commodity"></Commodity>
        </div>
    </ComPartition>
    <Footer/>
  </div>
</template>
<script>
import Carousel from "../../components/home/Carousel.vue"
import ComPartition from "../../components/other/ComPartition.vue"
import Commodity from '../../components/other/Commodity.vue'
import Top from '../../components/home/Top.vue'
import TopBar from '../other/TopBar.vue'
import Footer from '../../components/home/Footer.vue'
import SeckillTime from "../../components/seckill/SeckillTime.vue"
import SeckillCommodity from "../../components/seckill/SeckillCommodity.vue"
import axios from 'axios'
export default {
  name:'home',
  data() {
    return {
      sortList:[],
      seckillCommodityList:[]
    }
  },
  components: {
    Carousel,
    ComPartition,
    Commodity,
    TopBar,
    Top,
    Footer,
    SeckillTime,
    SeckillCommodity

  },
  mounted(){
    axios({
      method: 'get',
      url: '/getSortList?token=123'
    }).then((response) => {
      // console.log(response.data);
      this.sortList = response.data.result
      //因为这个sortList很多地方都在用，所以将他放到vuex中
    })

    axios({
      method: 'get',
      url: '/getSeckillCommodity'
    }).then((response) => {
      console.log(response.data);
      this.seckillCommodityList = response.data.result
      //因为这个sortList很多地方都在用，所以将他放到vuex中
    })
    
  }
};
</script>
<style scoped>
.out{
  background-color: rgb(241, 243, 245);
}
.seckill_commodity{
  display: flex;
}
</style>