<template>
    <div class="">
      <h4>{{ commodity.commodityName }}</h4>
      <!-- <p class="text-danger">{{ body }}</p> -->
      <div class="bg container">
        <!-- 促销价要写成计算属性 -->
        <div>
          <span class="jiage1">价&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;格</span>
          <h4 class="font-weight-bold text-danger jiage2">
            <span v-if="commodity.nowPrice != null && commodity.nowPrice != 0">
                <span>促销价￥{{commodity.nowPrice}}</span>
                <del>￥{{commodity.price}}</del>
            </span>
            <span v-else>
                <span class="oldprice">
                    ￥{{price}}
                </span>
            </span>
          </h4>
          <span style="color: green;font-size: 12px;" v-if="commodity.repertory > 20">
            库存充足
          </span>
          <span style="color: red;font-size: 12px;" v-if="commodity.repertory == 0">
            库存不足
          </span>
          <span style="color: orange;font-size: 12px;" v-if="commodity.repertory > 0 && commodity.repertory < 20">
            库存紧张
          </span>
        </div>
        <div class="d-flex foot">
          <div class="cuxiao">促&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;销</div>
          <div>
            <span v-if="commodity.nowPrice != null && commodity.nowPrice != 0">
              <span class="text-danger border border-danger s">
              限时直降
              </span>
              <span class="s2">
                限时直降{{discounts}}元
              </span>
              <br/>
            </span>
            <span class="text-danger border border-danger s">
              以旧换新抵现
            </span>
            <span class="s2">
              以旧换新至高补贴2022元
            </span><br />
            <span class="text-danger border border-danger s">
              整点赠礼
            </span>
            <span class="s2">
              整点赠华为手环4
            </span><br />
            <span class="text-danger border border-danger s">
              分期免息
            </span>
            <span
              class="text-danger border border-danger s">赠送积分
            </span>
          </div>
        </div>
      </div>
      <hr>
    </div>
  </template>
  
<script>
export default {
  data() {
    return {
      price : 0
    };
  },
  props:["commodity"],
  computed:{
    discounts(){
      return this.commodity.price - this.commodity.nowPrice
    },
    
  },
  methods:{
    
  },
  mounted() {
    //通过全局事件总线选择配置影响价格的事件的回调
    this.$bus.$on("changePrice",(price)=>{
      this.price = price
    })
  },
}

</script>

<style lang="css" scoped>
/* 背景颜色 */
.bg {
  background-color: #F5F5F5;
}

.jiage1 {
  font-size: 12px;
}

.jiage2 {
  display: inline-block;
  margin-top: 13px;
  margin-left: 24px;

}

.sss {
  color: #919191 !important;
  font-size: 18px;

}

.cuxiao {
  margin-top: 14px;
  font-size: 12px;
}

/* 灰色框 */
.s {
  display: inline-block;
  margin-top: 17px;
  margin-left: 24px;
  font-size: 12px;
  padding: 0px 6px;
}

.s2 {
  font-size: 12px;
  margin-left: 10px;
}

.foot {
  padding-bottom: 15px;
}

hr {
  margin-top: 5px;
}
</style>