<template>
  <div class="big">
    <!-- <h2>{{commofityOptions}}</h2>   -->
    <!-- 选择地区 -->
    <dl>
      <!--dl自带边距-->
      <label>服务说明</label>
      <div class="op">
        <select v-model="city">
          <option value="">请选择地区</option>
          <option value="北京">北京</option>
          <option value="上海">上海</option>
          <option value="武汉">武汉</option>
        </select>
      </div>
    </dl>
    <div class="baoyou">
      <span>√满48包邮</span>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span>√由华为终端提供商品、发货开票及售后服务</span>
      &nbsp;<a href="#">查看详情</a>
    </div>
    <div class="bigbianma">
      <span>商品编码</span><span class="bianma">{{commodity.id}}</span>
    </div>
    <hr />
    <dl v-for="(option,index) in commodityOptions" :key="index">
      <label>选择{{option.optionName}}</label>
      <div class="in">
        <span>
          <a :class="index==0?'one_active':'one'"
            @click="selcetOptin(option.optionName,item),changeSty($event,index),sendPrice()" href="#" class="one"
            v-for="(item,index) in option.options" :key="index">
            {{item.option}}
          </a>
        </span>
      </div>
    </dl>
    <dl>
      <label for="">已选择商品</label>
      <div class="select">
        <span v-for="(value,key) in selectedOptions" :key="key">
          {{value[1]}}
        </span>
      </div>
    </dl>

    <div class="footer d-flex">
      <!-- 计数器 -->
      <div class="d-flex number position-relative">
        <span class="position-absolute showLimit" v-show="showLimit">
          {{showMessage}}
        </span>
        <div class="shu">
          <!-- 购买数量  -->
          {{commodityNumber}}
        </div>
        <div>
          <button @click="commodityNumberChange('+')" class="jiahao font-weight-bold">+</button>
          <button @click="commodityNumberChange('-')" class="jianhao font-weight-bold">-</button>
        </div>
      </div>
      <!-- 提交按钮 -->
      <div class="container">
        <a @click="toShopCar">
          <input type="button" class="btn  jiaru" value="加入购物车" />
        </a>
        <a>
          <input @click="toPay" type="submit" ref="placeOrder" class="btn xiadan" value="立即下单" />
        </a>
      </div>
    </div>
  </div>
</template>

<script>
  import axios from 'axios';
  export default {
    data() {
      return {
        city: '',
        selectedOptions: new Map(),
        optionActive: [],
        commodityNumber: 1,
        showLimit: false,
        showMessage: '',
        commodity: '',
        commodityPrice: 0,
        optionPrice: 0
      };
    },
    // props:["commodity"],
    computed: {
      commodityOptions() {
        return this.commodity.jsonOptions
      },
      sendPriceValue() {
        return this.commodityPrice + this.optionPrice
      }
    },
    methods: {
      //option选项的相关操作 价格改变、样式改变等等操作
      selcetOptin(optionKey, item) {
        //首先将这个元素添加到已选参数中间
        this.selectedOptions.set(optionKey, item.option)
        this.$forceUpdate()//强制刷新页面否则，vue检测不到set集合中数据的变化
        //修改价格
        //使用全局事件总线将数据传递给组件CommodityRightTop  (这个组件中展示了价格)
        // this.$bus.$emit("changePrice",item.optionPrice)
        // this.sendPrice()
        //判断配置是否选择的是颜色  因为颜色会印象图片的展示 需要重新获取展示的图片
        if (optionKey == "颜色") {
          axios({
            method: 'get',
            url: '/getColorOptionImg/' + this.$route.params.commodityId + "/" + item.option
          }).then((response) => {
            //定义全局事件总线，将返回的对应颜色的图片传递给左侧土浦安轮播图
            this.$bus.$emit("images", response.data.result)
          })
        }
        //调用修改样式的方法
        // this.changeSty(event,index)
      },
      //封装出来的改变样式的操作
      changeSty(event, index) {
        var spans = event.target.parentElement.children
        for (var i = 0; i < spans.length; i++) {
          // console.log(spans[i]);
          if (i != index) {
            spans[i].className = "one"
          }
        }
        event.target.className = "one_active"
      },
      //传递配置价格的方法
      sendPrice() {
        this.optionPrice = 0
        for (let i = 0; i < this.commodityOptions.length; i++) {
          const option = this.commodityOptions[i]
          for (let j = 0; j < option.options.length; j++) {
            const element = option.options[j];
            // this.optionPrice
            this.selectedOptions.forEach((value, key, set) => {  //element
              if (value == element.option && (element.optionPrice != null && element.optionPrice != 0)) {
                this.optionPrice += parseFloat(element.optionPrice)
              }
            })
          }
        }
        this.$bus.$emit("changePrice", this.sendPriceValue * this.commodityNumber)
      },
      //商品数量改变的方法
      commodityNumberChange(operator) {
        if (operator == "+") {
          //判断是否限购
          if (this.commodity.limitNumber == this.commodityNumber) {
            this.showLimit = true
            this.showMessage = "当前商品限购" + this.commodity.limitNumber + "台"
            return
          }
          this.showLimit = false
          this.commodityNumber++

          //商品最小数量为1
        } else if (operator == "-") {
          if (this.commodityNumber <= 1) {
            this.showLimit = true
            this.showMessage = "至少购买一台"
          } else {
            this.showLimit = false
            this.commodityNumber--
          }
        }
        this.$bus.$emit("changePrice", this.sendPriceValue * this.commodityNumber)
      },
      toPay() {
        //将订单信息上传到后端
        //处理订单信息对象的json字符串
        if (localStorage.getItem("huawei_login") == null) {
          this.$router.push({
            path: '/login'
          })
          return
        }

        axios({
          method:'get',
          url:'/checkToken'
        }).then((response)=>{
          if(response.data.responseCode == 200){
            //判断是否付款成功
            if (confirm("是否成功付款？")) {
              this.sendData(0)
            } else {
              this.sendData(1)
            }
          }
        })
      },
      toShopCar() {
        const user = JSON.parse(localStorage.getItem("huawei_login"))
        if (user == null) {
          this.$router.push({
            path: '/login'
          })
        } else {
          axios({
          method:'get',
          url:'/checkToken'
        }).then((response)=>{
          if(response.data.responseCode == 200){
            this.addShopCar()
          }
        })
          
        }

      },
      //像后端发送订单信息
      sendData(Value) {
        //处理配置信息的字符串
        var strOptions = ''
        this.selectedOptions.forEach((value, key, set) => {
          strOptions += key + ":"
          strOptions += value + ","
        })
        axios({
          method: 'post',
          url: '/order/',
          params: {
            userId: this.$store.state.userState.id,
            orderPrice: this.sendPriceValue * this.commodityNumber,
            dfk: Value,
            commodityId: this.$route.params.commodityId,
            commodityOptions: strOptions,
            commodityNumber: this.commodityNumber,
            commodityPrice: this.sendPriceValue * this.commodityNumber
          }
        }).then((response) => {
          console.log(response.data);
          if (response.data.responseCode == 200 && Value == 0) {
            alert("购买成功")
          }else if(response.data.responseCode == 200 && Value == 1){
            alert("提交订单成功")
          }else if(response.data.responseCode == 201){
            alert(response.data.result.message)
          }
        })
      },
      //将商品添加到购物车的方法
      addShopCar() {
        var strOptions = ''
        this.selectedOptions.forEach((value, key, set) => {
          strOptions += key + ":"
          strOptions += value + ","
        })
        const user = JSON.parse(localStorage.getItem("huawei_login"))
        axios({
          method: 'post',
          url: '/addShopCar',
          params: {
            userId: user.id,
            scCommodityId: this.$route.params.commodityId,
            scCommodityOptions: strOptions,
            scCommodityNumber: this.commodityNumber,
            scCommodityPrice: this.sendPriceValue
          }
        }).then((response) => {
          //添加成功将新的订单
          console.log(response.data);
          alert('添加成功')
          this.$bus.$emit('sentShopCarCommoditys',response.data.result)
        })
      },
      //初始化价格 需要判断有没有活动降价
      initializePrice(price, nowPrice) {
        console.log(price, nowPrice);
        if (nowPrice == 0 || nowPrice == null) {
          this.commodityPrice = price
        } else {
          this.commodityPrice = nowPrice
        }
      }
    },
    watch: {
      //判断commodity的商品库存，设置是否禁用下单按钮
      commodity(newValue, oldValue) {
        if (newValue.repertory == 0 || newValue == null) {
          this.$refs.placeOrder.setAttribute("disabled", "true")
        }
      }
    },
    mounted() {
      //页面加载的时候调用一次选择颜色的方法 当前option是不是颜色的判断在selcetOptin中
      axios({
        method: 'get',
        url: '/commodityDetail/' + this.$route.params.commodityId
      }).then((response) => {
        this.commodity = response.data.result
        this.initializePrice(this.commodity.price, this.commodity.nowPrice)
        var options = response.data.result.jsonOptions
        this.$bus.$emit("changePrice", this.optionPrice + this.commodityPrice)
        for (let i = 0; i < options.length; i++) {
          this.selcetOptin(options[i].optionName, options[i].options[0])
        }
      })
    },
  }
</script>
<style lang="css" scoped>
  hr {
    margin-top: 7px;
  }

  /* 最外围div */
  .big {
    margin-left: 17px;
  }

  /* 遍历的a标签 */
  /* 选择颜色 */
  .one {
    /* display:inline-block:行级块元素 */
    display: inline-block;
    height: 30px;
    line-height: 30px;
    font-size: 12px;
    margin-left: 10px;
    color: black;
    /* 把此元素放在父元素中部 */
    vertical-align: middle;
    border: 1px solid #a4a4a4;
    text-align: center;
    border-radius: 2px;
    padding: 0 12px;
    text-decoration: none;
    margin-bottom: 10px;

  }

  .one_active {
    /* display:inline-block:行级块元素 */
    display: inline-block;
    height: 30px;
    line-height: 30px;
    font-size: 12px;
    margin-left: 10px;
    color: black;
    /* 把此元素放在父元素中部 */
    vertical-align: middle;
    border: 1px solid red;
    text-align: center;
    border-radius: 2px;
    padding: 0 12px;
    text-decoration: none;
    margin-bottom: 10px;
  }

  .one:hover {
    border: 1px solid red;
  }

  /* 选择套餐 */
  .two {
    display: inline-block;
    margin-left: 27px;
    height: 30px;
    vertical-align: middle;
    border: 1px solid #a4a4a4;
    text-align: center;
    border-radius: 2px;
    position: relative;
    padding: 0px 12px;
    overflow: hidden;
    text-decoration: none;
  }

  .two:hover {
    border: 1px solid red;
  }

  img {
    width: 30px;
    height: 30px;
    margin-top: 4px;
  }

  /* .sp1 {
  color: black;
  vertical-align: middle;
  font-size: 12px;
} */

  .sp2 {
    color: black;
    font-size: 12px;
  }

  .in {
    display: inline-block;
  }

  /* 服务说明 */
  /* 选择地区下拉列表 */
  .op {
    display: inline-block;
    margin-left: 27px;
    margin-top: 0px;
    font-size: 12px;
  }

  .baoyou {
    margin-left: 75px;
    font-size: 12px;
  }

  /* 保障服务 */
  .fw {
    display: inline-block;
    font-size: 12px;
  }

  .fw span {
    display: inline-block;
    margin-left: 27px;
  }

  /* 已选择商品 */
  .select {
    display: inline-block;
    margin-left: 17px;
    color: red;
    font-size: 12px;
  }

  /* 商品编码 */
  .bigbianma {
    margin-top: 17px;
  }

  .bianma {
    margin-left: 27px;
  }

  /* 计数器 */
  .number {
    width: 70px;
    border: 1px solid #a4a4a4;
    border-radius: 2px;
    border-right: #ffffff;
  }

  .shu {
    width: 42px;
    height: 44px;
    text-align: center;
    padding-top: 7px;
    font-size: 20px;
  }

  .jiahao,
  .jianhao {
    display: block;
    width: 21px;
    height: 22px;
    text-align: center;
    border: 1px solid #a4a4a4;
    border-top: #ffffff;
    border-radius: 2px;
    color: black;
    margin-top: 0px;
    text-decoration: none;
  }

  .jiaru {
    height: 46px;
    width: 172px;
    background-color: #fc660c;
    color: #ffffff;
  }

  .jiaru:hover {
    height: 46px;
    width: 172px;
    background-color: #fc660c;
    color: #ffffff;
  }

  .xiadan {
    height: 46px;
    width: 171px;
    margin-left: 17px;
    background-color: #CA151D;
    color: #ffffff;
  }

  .xiadan:hover {
    height: 46px;
    width: 171px;
    margin-left: 17px;
    background-color: #CA151D;
    color: #ffffff;
  }

  label,
  .bigbianma {
    font-size: 12px;
  }

  .showLimit {
    height: 20px;
    width: 200px;
    top: -20px;
    font-size: 12px;
    color: red;
  }

  .disableButton {
    pointer-events: none;
  }
</style>