<template>
    <div>
        <CommDetailRightTop :commodity="commodity"/>
        <SeckillRightButton  :commodity="commodity"/>
    </div>
    </template>
    <script>
    import CommDetailRightTop from '../../components/other/CommDetailRightTop.vue'
    import SeckillRightButton from './SeckillRightButton.vue'
    import axios from 'axios'
    export default{
        data() {
            return {
                commodity:{},
                
            }
        },
        components:{
            CommDetailRightTop,
            SeckillRightButton
        },
        mounted(){
            axios({
                method:'get',
                url:'/commodityDetail/' + this.$route.params.commodityId
            }).then((response) => {
                // console.log(response.data.result);
                this.commodity = response.data.result
            })
        },
        computed:{
            pay_get_price(){
                return this.commodity.nowPrice == null || this.commodity.nowPrice == 0?this.commodity.price:this.commodity.nowPrice
            }
        },
    }
    </script>
