<template>
    <div class="out">
        <div class="one_box">
            <img class="logo_img" src="/images/pay/绿色logo.png">
            <span class="log_text">收银台</span>
        </div>
        <div class="second_box">
            <div>订单金额{{price}}</div>
            <div>应付金额{{price}}</div>
            <div>订单类型{{price}}</div>
        </div>
        <div class="third_box">
            <div>
                <img class="logo_img" src="/images/pay/WePayLogo.png">
                <img class="button_img" src="/images/pay/按钮标签.png">
                <span>亿万用户的选择，更快更安全</span>
            </div>
            <div>
                <p>
                    支付{{price}}元
                </p>
            </div>
        </div>
        <img src="/images/pay/wx.jpg" alt="">
    </div>
</template>
<script>
import axios from 'axios'
export default {
    name:'pay',
    data() {
        return {
            price:0
        }
    },
    mounted() {
        axios({
            methos:"get",
            url:'/pay/'+this.$route.params.commodityId
        }).then((response)=>{
            var result = response.data.result
            if(result.nowPrice == null || result.nowPrice==0){
                this.price = result.price
            }else{
                this.price=result.nowPrice
            }
        })
    },
    computed:{
    }
}
</script>
<style scoped>
.out{
    width: 800px;
    margin: auto;
}
.logo_img{
    height: 40px;
    margin: 0 10px 0 10px;
}
.log_text{
    font-size: 30px;
    line-height: 40px;
}
.one_box{
    display: flex;
    margin-top: 10px;
}
.second_box{
    height: 170px;
    padding: 50px;
    margin-top: 20px;
    background-color: rgb(242, 242, 242);
}
.second_box div:nth-child(2){
    text-align: end;
}
.third_box{
    display: flex;
    justify-content: space-between;
}
.third_box div:nth-child(n=1n=2){
    display:flex;
    justify-content:center;
    /* 竖直对齐 */
    align-items: center;
}
.third_box span:last-child{
    font-size: 14px;
    color: rgb(161, 161, 161);
}
.button_img{
    height: 20px;
}
.out img:last-child{
    height: 300px;
}
</style>