<template>
    <div>
        <div class="topover">
            <div class="top">
                <div class="container">
                    <div class=" text-center mt-5 pt-5 h2">
                        华为账号登录
                    </div>

                    <div class=" text-center d-flex justify-content-center mt-5 pt-5">
                        <div class="row">
                            <div class="a8 col">
                                <img class="shadow rounded" src="/images/login/erweima.png" />
                                <p class="a1 mt-5 pt-2">若您使用华为手机，请进入“设置”>“华为帐号”扫码登录。</p>
                            </div>
                            <div class="a3 col ">
                                <!-- <form action="" class=""> -->
                                <input type="text" class="a2 mb-4 ml-5 form-control" v-model="name"
                                    placeholder="手机号/邮件地址/账号名">
                                <input type="password" class="a2 ml-5 mb-3 form-control" v-model="password"
                                    placeholder="密码">
                                <div class="a5 ml-5 mt-4  form-control" @click="login">登录</div>
                                <!-- </form> -->
                                <div class="a6 mt-4 ">
                                    <span class=" pr-3">
                                        <router-link to="/register" replace>
                                            没有账号?立即注册
                                        </router-link>
                                    </span>
                                </div>
                                <div class="a6 mt-4 ">
                                    <a href="#"> <img class="rounded-circle" src="/images/login/ali.png" /></a>
                                    <a href="#"> <img class="rounded-circle" src="/images/login/qq.png" /></a>
                                    <a href="#"> <img class="rounded-circle" src="/images/login/weixin.png" /></a>
                                </div>
                            </div>
                        </div>

                    </div>


                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import axios from 'axios';
    export default {
        //给组件起一个名字,开发者工具中显示这个组件的时候，显示的就是这个名字
        name: "login",
        data() {
            return {
                name: '',
                password: '',
            };
        },
        methods: {
            login() {
                axios({
                    url: '/user/' + this.name + '/' + this.password,
                    method: 'get',
                }).then((response) => {
                    const responseData = response.data
                    console.log(responseData.result);
                    if (responseData.responseCode == 200) {
                        //登录成功首先将用户信息存入本地储存中
                        // this.$store.dispatch("setUser",responseData.result)
                        localStorage.setItem("huawei_login", JSON.stringify(responseData.result))
                        console.log("获取到的token",responseData.result.token);
                        localStorage.setItem("huawei_token", JSON.stringify(responseData.result.token))
                        this.$store.state.userState = responseData.result
                        this.$router.push({
                            path:'/home'
                        })

                    } else if (responseData.responseCode == 201) {
                        alert('用户名不存在')
                    } else if (responseData.responseCode == 202) {
                        alert('密码错误')
                    }
                })

            }
        },
        mounted() {
        },

    };
</script>

<style scoped>
    .topover {
        width: 100%;
        background: rgb(242, 242, 242);
    }

    /* .top {
    width: 1200px;
} */
    .container {
        width: 1200px;
        height: 740px;
        margin: 0 auto;
        overflow: hidden;
    }

    /*规范页面最小宽度1200px */

    .a1 {
        font-size: 13px;
    }

    .a2 {
        height: 48px;
        width: 358px;

    }

    .a3 {
        margin-left: 60px;
        padding-left: 60px;
        border-left: solid 1px rgb(141, 137, 137);
    }

    .a3 input {
        border: 0px;
    }

    .a3 input:focus {
        box-shadow: none;
    }


    .a4 {
        font-size: 14px;
        float: left;
        text-decoration: none;

    }

    .a4:hover {
        font-size: 15px;
        float: left;
        text-decoration: none;

    }


    .a5 {
        width: 360px;
        height: 48px;
        color: aliceblue;
        background-color: red;
        font-size: 18px;

    }

    .a6 a {
        text-decoration: none;
        font-size: 18px;
        padding: 0px 20px 0px 20px;
    }

    .a7 {
        border-left: 1px solid;
        border-color: rgb(36, 36, 36);
    }
</style>