<template>
<div class="out">
    <div v-show="hasCommodity">
        没有符合条件的数据呢,换个关键词试试吧!
    </div>
    <Commodity v-for="commodity in commodityList" :key="commodity.id" :commodity="commodity"></Commodity>
</div>
</template>
<script>
import Search from '../other/Search.vue';
import Commodity from '../../components/other/Commodity.vue';
export default{
    data() {
        return {
            commodityList:[],
            hasCommodity:false
        }
    },
    components:{
        Search,
        Commodity
    },
    mounted(){
        console.log(this.commodityList.length);
        this.$bus.$on("sendSearchResult",(commodityList)=>{
            console.log("接收到的参数",commodityList);
            this.commodityList = commodityList
            if(commodityList.length == 0){
                this.hasCommodity = true
            }else{
                this.hasCommodity = false
            }
        })
    }
}
</script>
<style scoped>
.out{
    width: 1212px;
    margin: auto;
    min-height: 800px;
}
</style>