import vue from 'vue'
import vuex from 'vuex'

vue.use(vuex)

//创建Actions用于响应组件中的动作
const actions = {
    setUser(context, user){
        context.commit("ADDUSERSTATE",user)
    }
}
//真正用于操作数据的（回调）
//这里面键值对的值必须是一个函数
const mutations = {
    ADDUSERSTATE(state,user){
        // console.log(context);
        state.userState = user
    }
}
//状态存储数据
const state = {
    userState:null,
    myorder:{
        orders:"woshi 测试的order"
    }
}


//创建store并暴露store
export default new vuex.Store({
    //对象的键和值重名，可以触发简写形式
    actions,
    mutations,
    state
})